body {
  height: 100vh;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  /* background-color: lightgray; */
  background: url('./images/background.jpg');
  background-size: 100vw;
}

.board-container {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.title {
  height: 20px;
  display: block;
  margin: 10px 0 0 38%;
  position: fixed;
  padding: 0;
  img {
    width: 50%;
  }
}

.square {
  background-color: white;
  width: 250px;
  height: 250px;
  border: 2px solid black;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  font-size: 72px;
  color: rgba(0, 0, 0, 0.8);
  box-shadow: -6px -6px 20px 2px;
  transition: background-color 0.7s ease;
}

.square:hover {
  background-color: rgba(255, 0, 0);;
}

.winner-container {
  width: 100%;
  height: 100vh;
  position: fixed;
  text-align: center;
  font-size: 52px;
  color:orangered;
  background-color: rgba(0, 0, 0, 0.7);
  font-family:Arial, Helvetica, sans-serif
}

.winner-message {
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: white;
  margin-top: 400px;
  user-select: none;
  margin-left: 70%;
}

.restart-button {
  width: 10%;
  height: 50px;
  border: 0px solid transparent;
  border-radius: 20px;
  background-color: lightgreen;
  margin-left: 70%;
  cursor: pointer;
  user-select: none;
  font-size: 24px;
  letter-spacing: 1px;
  transition: border-width 0.3s ease, border-color 0.3s ease, background-color 0.5s ease;
}

.restart-button:hover {
  background-color: greenyellow;
}